.songContainer {
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    img {
        box-shadow: rgba(0, 0, 0, 0.2) 0px 12px 28px 0px, rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(255, 255, 255, 0.05) 0px 0px 0px 1px inset;
        width: 25%;
        margin-top: 30px;
        border-radius: 50%;
        animation: spin 2s 0 ease-in 1, spin 1.5s 2s linear infinite;
        cursor: pointer;
    }
    @keyframes spin {
        100% {
            transform: rotate(360deg);
        }
    }
    h2 {
        padding: 3rem 1rem 1rem 1rem;
    }
    h3 {
        font-size: 1rem;
    }
}

.active-song {
    img {
        width: 25%;
        margin-top: 30px;
        border-radius: 50%;
        animation-name: rotate;
        box-shadow: rgba(0, 0, 0, 0.2) 0px 12px 28px 0px, rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(255, 255, 255, 0.05) 0px 0px 0px 1px inset;
        animation-duration: 1800s;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
        cursor: pointer;
    }
    @keyframes rotate {
        from {
            transform: rotate(-360deg);
        }
        to {
            transform: rotate(360deg);
        }
    }
}

@media screen and (max-width: 768px) {
    .songContainer {
        img {
            width: 75%;
            margin-top: 30px;
            border-radius: 50%;
        }
        h2 {
            padding: 3rem 1rem 1rem 1rem;
        }
        h3 {
            font-size: 1rem;
        }
    }
}