* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
    user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -moz-user-select: none;
    .App-Footer {
        color: rgba(0, 0, 0, 0.7);
        text-shadow: 0 1px rgba(255, 255, 255, 0.1);
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 12px;
        margin-bottom: 20px;
    }
    body {
        font-family: "Manrope", sans-serif;
    }
    h3 {
        font-weight: 500;
        color: rgb(41, 41, 41);
    }
    h4 {
        font-weight: 400;
        color: gray;
    }
}

.App {
    transition: all 0.3s ease-out;
}

.library-active {
    margin-left: 28%
}

@media screen and (max-width: 768px) {
    .App-Footer {
        font-size: 10px;
        margin-top: 45px;
    }
}

@import "./song";
@import "./player";
@import "./library";
@import "./nav";